.card {
    width: 100%;
    background: #ffffff;
    border: 1px solid #f4f4f4;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04),
        0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 12px;
}
.card_info {
    width: 100%;
    padding: 16px;
    padding-bottom: 32px;
}
.card_image {
    width: 100%;
}
.title_rating_container {
    padding-top: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.title {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    color: #0a0a0a;
}

.description {
    padding-top: 32px;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #0a0a0a;
}
.button_container {
    display: flex;
    justify-content: right;
    padding-top: 32px;
}
