.rating_container {
    display: flex;
    gap: 24px;
}
.rating {
    display: flex;
    align-items: center;
    gap: 8px;
}
.rating_icon {
    width: 32px;
    height: 32px;
    cursor: pointer;
}
.rating_Count {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: #4f4f4f;
}
